anchas se deben desmontar las bases y revisar los conductos por donde va el agua y revisar los tanques o calderines que producen el vapor. Además, las máquinas tienen identificados los puntos por donde se les debe proveer el lubricante y cuando se termina el mantenimiento se debe ensayar cada máquina para asegurar que quedó funcionando después de la intervención.")])]),_c('div',{staticClass:"col-lg-3 mb-3"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema_2/img_7.png"),"alt":"Texto que describa la imagen"}})])]),_c('p',[_vm._v("También es importante mantener en inventario las piezas pequeñas que requieran ser sustituidas de manera periódica para evitar que se detenga la producción, este ejercicio debe quedar registrado. En resumen, todas las actividades enfocadas a evitar que las máquinas dejen de funcionar de manera intempestiva, mejor dicho, todo lo que se haga para que el equipo no deje de funcionar, se puede catalogar como mantenimiento preventivo.")]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-11"},[_c('p',{staticClass:"BorTit"},[_c('strong',[_vm._v("Figura 21")]),_vm._v(" "),_c('br'),_vm._v("Ficha técnica de mantenimiento preventivo")]),_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema_2/img_8.png"),"alt":"Texto que describa la imagen"}}),_c('figcaption',[_vm._v("Nota. Tomada de Cabanillas (2018)")])])])])]),_c('div',{staticClass:"row",attrs:{"numero":"3","titulo":"Mantenimiento correctivo"}},[_c('div',{staticClass:"col-lg-12"},[_c('p',[_vm._v("Cuando de manera desafortunada se presentan situaciones de mecánica que no se pudieron evitar con el mantenimiento preventivo, se tendrá que recurrir a un arreglo más especializado y del cual se hará cargo una mano de obra más técnica, con el fin de tener un diagnóstico e intervención más profesional, de la reparación o el reemplazo de una pieza. Es importante para ayudar a detectar el daño, tener las evidencias de las últimas costuras que realizó, 